import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import BackToTop from 'vue-backtotop'
import cookie from 'cookie'
import SiteNotice from '~/components/SiteNotice'
import ScoreRank from '~/components/ScoreRank'
import FriendLinks from '~/components/FriendLinks'
import TopicsNav from '~/components/TopicsNav'
import TopicList from '~/components/TopicList'
import TweetsWidget from '~/components/TweetsWidget'
import Pagination2 from '~/components/Pagination2'
import MetroSelector from '~/components/MetroSelector'
import utils from '~/common/utils'
// import Panel from '~/components/Panel'
library.add(faAngleUp, faAngleDown)
export default {
  components: {
    // Carousel: () =>
    //   import('vue-carousel')
    //     .then((m) => m.Carousel)
    //     .catch(),
    // Slide: () =>
    //   import('vue-carousel')
    //     .then((m) => m.Slide)
    //     .catch(),
    FontAwesomeIcon,
    BackToTop,
    SiteNotice,
    ScoreRank,
    FriendLinks,
    // TopicsNav,
    TopicList,
    TweetsWidget,
    Pagination2,
    MetroSelector
  },
  async asyncData({ $axios, query, req }) {
    let city = '北京'
    if (req && req.headers !== undefined) {
      const cookies = cookie.parse(req.headers.cookie || '')
      if (cookies && cookies !== '') {
        city = cookies.city
      }
    }
    if (city === undefined) {
      city = '北京'
    }
    // console.log('city:' + city)
    try {
      const [topicsPage, scoreRank, links, newestTweets] = await Promise.all([
        $axios.get('/api/topic/node/topics', {
          params: {
            page: query.p || 1,
            city,
            nodeId: 2
          }
        }),
        $axios.get('/api/user/score/rank'),
        $axios.get('/api/link/toplinks'),
        $axios.get('/api/tweet/newest')
      ])
      return { topicsPage, scoreRank, links, newestTweets }
    } catch (e) {
      console.error(e)
    }
  },
  data() {
    return {
      searchTerms: '',
      searchAction: false,
      rentPagingAction: '/api/topic/node/topics',
      tagPagingAction: '/api/topic/tag/topiclist',
      pageAction: '',
      pageParmsContext: {}
    }
  },
  mounted() {
    if (utils.getCity() === '武汉') {
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6183'
    }
    if (utils.getCity() === '北京') {
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6346'
    }
    if (utils.getCity() === '杭州') {
      this.bannerImgurl =
        'https://upload-images.jianshu.io/upload_images/17856673-382c2bfa30d1d02f.JPG'
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6183'
    }
    if (utils.getCity() === '上海') {
      this.jumpCityUrl = 'https://sharevideo.cn/rent/6347'
    }

    this.pageAction = this.rentPagingAction
  },
  methods: {
    onPageChanged(pageAction, page, parmsContext) {
      let params = {}
      if (pageAction === this.rentPagingAction) {
        params = this.getRentParms(page)
      }
      if (pageAction === this.tagPagingAction) {
        params = {
          page,
          city: utils.getCity(),
          tagName: parmsContext.tagName
        }
      }

      this.changePageTopics(pageAction, params, null)
    },
    getRentParms(page) {
      return {
        page: page || 1,
        city: utils.getCity(),
        nodeId: 2
      }
    },
    onTagChangedCallBack(tag) {
      // if (tag === '不限') {
      //   this.pagingAction = '/rents?p='
      // } else {
      //   this.pagingAction = '/topics/tag?p='
      // }
      if (tag === '不限') {
        this.pageAction = this.rentPagingAction
        this.changePageTopics(
          this.rentPagingAction,
          this.getRentParms(1),
          () => {
            this.refreshPagination()
          }
        )
      } else {
        this.tagSearchTopics(tag)
      }

      // console.log('metro tag:' + tag)
    },
    onCollapseBack(state) {
      // console.log('onCollapse:' + state)
    },
    jumpCityWechatUrl() {
      window.location.href = this.jumpCityUrl
    },
    jumpUrl(href) {
      if (href) {
        window.open(href)
      } else {
      }
    },
    twitterCreated(data) {
      if (this.topicsPage) {
        if (this.topicsPage.results) {
          this.topicsPage.results.unshift(data)
        } else {
          this.topicsPage.results = [data]
        }
      }
    },
    changePageTopics(url, params, callback) {
      this.$axios
        .get(url, { params })
        .then((res) => {
          this.topicsPage.results = res.results
          this.topicsPage.page = res.page
          if (callback) {
            callback()
          }
        })
        .catch((e) => {
          console.log('tagSearchTopics error:' + JSON.stringify(e))
        })
    },
    refreshPagination() {
      const state = this.searchAction
      this.searchAction = !this.searchAction
      this.$nextTick(() => {
        this.searchAction = state
      })
    },
    tagSearchTopics(tagName) {
      this.pageAction = this.tagPagingAction
      this.pageParmsContext = {
        page: 1,
        tagName
      }
      this.changePageTopics(this.tagPagingAction, this.pageParmsContext, () => {
        this.refreshPagination()
      })

      // this.$axios
      //   .get('/api/topic/tag/topiclist', {
      //     params: {
      //       page: 1,
      //       tagName
      //     }
      //   })
      //   .then((res) => {
      //     this.topicsPage.results = res.results
      //     this.topicsPage.page = res.page
      //   })
      //   .catch((e) => {
      //     console.log('tagSearchTopics error:' + JSON.stringify(e))
      //   })
    },
    // async search() {
    //   try {
    //     await this.$axios.post('/api/user/edit/' + this.user.id, {
    //       nickname: this.form.nickname,
    //       avatar: this.form.avatar,
    //       homePage: this.form.homePage,
    //       description: this.form.description
    //     })
    //     await this.reload()
    //     this.$toast.success('资料修改成功')
    //   } catch (e) {
    //     console.error(e)
    //     this.$toast.error('资料修改失败：' + (e.message || e))
    //   }
    // },
    search() {
      if (utils.isEmpty(this.searchTerms.trim())) {
        this.$toast.error('请输入租房地点')
        return
      }

      this.$axios
        .get('/api/topic/search', {
          params: {
            terms: this.searchTerms,
            city: utils.getCity()
          }
        })
        .then((res) => {
          this.topicsPage.results = res
          this.searchAction = true
        })
        .catch((e) => {
          console.log('search error:' + JSON.stringify(e))
        })
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区-出租/转租/合租找室友'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
